import React from "react"
import PropTypes from "prop-types"

const Header = ({title}) => {
    
    return (
        <div>
            <header className="h-50 bg-white border-b-2 py-5 shadow-sm px-3 sm:px-10 flex justify-between text-center">
                <h1 className="font-bold text-2xl" >{title}</h1>
            </header>
        </div>
    )
}

// const headerTextStyle = {
//     color: 'red',
//     backgroundColor: 'black'
// }


Header.defaultProps = {
    title: 'Task Tracker'
}

Header.propTypes = {
    title: PropTypes.string.isRequired,
}


export default Header
