import React, {useState, useEffect} from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Button from './Button'
import Task from './Tasks'
import AddTask from './AddTask'
import Footer from './Footer'
import About from './About'





const Content = () => {
    const [showAddTask, setShowAddTask] = useState(false)

    const [tasks, setTasks] = useState([])

    useEffect(()=>{
        const getTasks = async () =>{
           const taskFromServer = await fetchTasks()
           setTasks(taskFromServer)
        }
        getTasks()
    }, [])

    const fetchTasks = async () => {
        const res = await fetch('http://localhost:5000/tasks')
        const data = await res.json()
        return data;
    }
    
    const fetchTask = async (id) => {
        const res = await fetch(`http://localhost:5000/tasks/${id}`)
        const data = await res.json()
        return data;
    }

    const addTask = async (task) => {
        const res = await fetch('http://localhost:5000/tasks',{
            method: 'POST',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify(task)
        })

        const data = await res.json()
        setTasks([...tasks, data])

        
    }

    const deleteTask = async (id) => {
        await fetch(`http://localhost:5000/tasks/${id}`,{
            method: 'DELETE'
        })
        setTasks(tasks.filter((task)=>task.id !== id))
    }

    const toggleReminder = async (id) => {
        const taskToogle = await fetchTask(id)
        const upTask = {...taskToogle,
        reminder: !taskToogle.reminder}

        const res = await fetch(`http://localhost:5000/tasks/${id}`,{
            method: 'PUT',
            headers: {
                'Content-type': 'application/json',
            },
            body: JSON.stringify(upTask),
        })

        const data =  await res.json()

        //console.log(data.reminder);
        setTasks(
            tasks.map((task)=> 
            task.id === id ? {...task, reminder: 
                data.reminder } : task
            )
        )
       
    }
    return (
        <Router>
                <div className="max-w-xl mx-auto px-2 pb-10">
                    <div className="mt-10 w-full flex justify-between items-center bg-white shadow-sm py-2 px-4 rounded-sm">
                        <label className="font-semibold">Task</label>
                        <Button btnClickAdd={()=>setShowAddTask(!showAddTask)} showAdd={showAddTask}  />
                    </div>
                   
                    <Route path='/' exact render={(props)=>(
                        <>
                             {showAddTask && <AddTask onAdd={addTask} />}
                            {tasks.length > 0 ? <Task tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} /> : <div className="text-center w-full text-gray-300 mt-10 font-semibold">No Task available</div> }
                        </>
                    )} />
                    <Route path='/about' component={About}  />
                    <Footer />
                </div>
        </Router>    
    )
}

export default Content
