import React, {useState} from 'react'

const AddTask = ({onAdd}) => {
    const [text, setText] = useState('')
    const [date, setDate] = useState('')
    const [time, setTime] = useState('')
    const [reminder, setRemind] = useState(false)

    const onSubmit = (e) =>{
        e.preventDefault()
        if(!text){
            alert('Please add a task')
            return
        }

        const day = date+' '+time;
    
        onAdd({text, day, reminder})

        setText('')
        setDate('')
        setTime('')
        setRemind(false)
    }

    return (
        <div className="mt-5 py-5 px-4 bg-white shadow-sm mx-auto">
            <form onSubmit={onSubmit} >
                <div className="my-2">
                    <p className="text-sm font-semibold">Name</p>
                    <input type="text" className="w-full rounded-sm bg-white px-2 py-1 border border-gray-200" value={text} onChange={(e)=> setText(e.target.value)} />
                </div>
                <div className="my-2">
                    <p className="text-sm font-semibold">Date and Time</p>    
                    <div className="inline-flex space-x-4">
                        <input type="date" className="w-full rounded-sm bg-white px-2 py-1 border border-gray-200"  value={date} onChange={(e)=> setDate(e.target.value)}/>
                        <input type="time" className="w-full rounded-sm bg-white px-2 py-1 border border-gray-200" value={time} onChange={(e)=> setTime(e.target.value)}/>
                    </div>
                </div>
                <div className="my-2 inline-flex spance-x-2 items-center">
                    <p className="text-sm mr-2 font-semibold">Remind Me</p>
                    <input type="checkbox" className="cursor-pointer" checked={reminder} value={reminder} onChange={(e)=> setRemind(e.currentTarget.checked)} />
                </div>
                <div className="my-2">
                    <button className="w-full bg-blue-600 text-white rounded-sm py-2 text-sm">Save Task</button>
                </div>
            </form>
        </div>
    )
}

export default AddTask
