import PropTypes from 'prop-types'
import { useLocation } from 'react-router-dom'

const Button = ({text, btnClickAdd, showAdd}) => {
    const location = useLocation()
    
    return (
        <div>
             {location.pathname === '/' && (
                 <button onClick={btnClickAdd} className="bg-blue-500 text-sm text-white py-1 px-3 rounded-sm hover:bg-blue-600 font-light">{showAdd ? 'Close': 'Add'}</button>
             )}
        </div>
    )
}

Button.propTypes = {
    btnClick: PropTypes.func
}
export default Button
