import {FaTimes} from 'react-icons/fa'

const Task = ({list, onDelete, onToggle}) => {
    const dt = list.day.split(' ');
    return (
        <div>
            <div onDoubleClick={()=>onToggle(list.id)} className={`task ${list.reminder ? 'border-l-2 border-blue-500 reminder' : 'reminder'}`}>
                <FaTimes onClick={()=>onDelete(list.id)} className="float-right text-red-600 cursor-pointer hover:text-red-900" />  
                <h3>{list.text}</h3>
                <small>{ new Date(dt[0]).toDateString() + ' - '+dt[1] }</small>
            </div>
            
        </div>
    )
}

export default Task
